
type Props = {
	data: SectionData;
	children?: React.ReactNode;
}

function Section(props: Props) {
	const { id, logo, title, dates, info, bgColor = '', bgImage } = props.data;
	return <section className={`section ${id}`} style={{
		backgroundColor: bgColor
		}}>
		<div className='bgImage' style={{  backgroundImage: `url('${bgImage}')`}} />
		<div className='wrapper'>
			<div className='text-container'>
				<img src={logo} alt="logo" height="60"/>
				<div className='section-title'>	
					<span>{title}</span>
					<span className='section-dates'>{dates}</span>
				</div>
				<div className='section-info'>{info}</div>
			</div>
		</div>
	</section>;
}

type SectionData = {
	id: string;
	logo: string;
	title: string;
	dates: string;
	info: string;
	bgColor?: string;
	bgImage?: string
}

export default Section;
